/*__________________________________________________________________________________

Buttons

__________________________________________________________________________________*/
.controls {
  width: 100%;
  max-width: 300px;
  text-align: center;
  position: absolute;
  top: 97%;
  left: 0%; }

.btn {
  min-width: 75px;
  background-color: red;
  display: inline-block;
  border-top: 5px solid #e6b800;
  border-bottom: 5px solid #e6b800;
  border-left: 5px solid #e6b800;
  border-right: 5px solid #e6b800;
  border-radius: 20px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  padding: 20px 5px;
  color: white;
  font-weight: 700;
  white-space: nowrap; }
  .btn:focus {
    outline: none; }
  .btn:hover {
    background-color: lawngreen;
    cursor: pointer; }

/*__________________________________________________________________________________

End Buttons

__________________________________________________________________________________*/
/*__________________________________________________________________________________

Welcome Hi

__________________________________________________________________________________*/
.hi {
  width: 50px;
  height: 72px;
  background-image: url("../../assets/sprite/hello.png");
  -webkit-animation: play 0.8s steps(10) infinite;
          animation: play 0.8s steps(10) infinite;
  margin: auto; }

@-webkit-keyframes play {
  from {
    background-position: 0; }
  to {
    background-position: -500px; } }

@keyframes play {
  from {
    background-position: 0; }
  to {
    background-position: -500px; } }
.gif-container img {
  position: relative;
  max-width: 270px; }

/*__________________________________________________________________________________

Welcome Hi  end

__________________________________________________________________________________*/
/*__________________________________________________________________________________

Stats

__________________________________________________________________________________*/
.stats__wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex: 1 1 250px;
      flex: 1 1 250px;
  padding: 10px 10px 5px 10px; }

.stats {
  color: white;
  margin: 0;
  -ms-flex: 1 1 125px;
      flex: 1 1 125px;
  padding: 5px 0; }

/*__________________________________________________________________________________

Stats end

__________________________________________________________________________________*/
/*__________________________________________________________________________________

Messages block

__________________________________________________________________________________*/
.alert {
  color: red;
  margin: 0; }

.info {
  color: skyblue;
  margin: 0 auto;
  word-break: break-word;
  max-width: 200px; }



